import React from 'react';
import { AnimatePresence, motion } from 'framer-motion';

import classes from './MenuButton.module.scss';

const buttonVariants = {
  start: {
    y: '-7%',
    scale: 1.1,
    originX: 0,
    originY: 0.5,
    perspective: '100rem',
    backfaceVisibility: 'hidden',
    transform: 'translate3d(0, 0, 0)',
    transformStyle: 'preserve-3d',
  },
  end: {
    y: '7%',
    scale: 1,
    transition: {
      type: 'tween',
      duration: 0.5,
      ease: 'easeInOut',
      repeat: Infinity,
      repeatType: 'reverse',
    },
  },
};

const menuBackgroundVariants = {
  init: {
    scale: 0,
    originX: 0.35,
    originY: 0.83,
  },
  open: {
    scale: 1,
    transition: {
      type: 'spring',
      stiffness: 150,
    },
  },
  close: {
    scale: 0,
  },
};

const rodVariants = {
  init: {
    scale: 0,
    originX: 0.5,
    originY: 1,
  },
  open: {
    scale: 1,
    transition: {
      type: 'spring',
      stiffness: 150,
      delay: 0.4,
      delayChildren: 0.7,
    },
  },
  close: {
    scale: 0,
  },
};

const rodChildrenVariants = {
  init: {
    scaleX: 0,
    opacity: 0,
    originX: 0,
    originY: 0.5,
  },
  open: {
    scaleX: 1,
    opacity: 1,
    transition: {
      type: 'spring',
      stiffness: 150,
    },
  },
};

const menuButton = ({ clicked, closed = true, noOfBranches }) => {
  const classList = [classes.MenuButton];
  const branchList = [];

  let menuBackground = null;
  let rod = null;
  if (!closed) {
    classList.push(classes.active);
    menuBackground = (
      <motion.div
        className={classes.MenuBackground}
        variants={menuBackgroundVariants}
        initial="init"
        animate="open"
        exit="close"
      ></motion.div>
    );
    rod = (
      <motion.div
        className={classes.Rod}
        variants={rodVariants}
        initial="init"
        animate="open"
        exit="close"
      >
        <motion.div
          className={classes.BranchList}
          variants={rodChildrenVariants}
        >
          {branchList}
        </motion.div>
      </motion.div>
    );
  }

  for (let i = 0; i < noOfBranches; i++) {
    branchList.push(<div key={i}></div>);
  }

  return (
    <div className={classes.MenuButtonContainer}>
      <AnimatePresence>{menuBackground}</AnimatePresence>
      <AnimatePresence>{rod}</AnimatePresence>
      <motion.button
        className={classList.join(' ')}
        onClick={clicked}
        variants={buttonVariants}
        initial="start"
        animate="end"
      >
        <div></div>
        <div></div>
        <div></div>
      </motion.button>
    </div>
  );
};

export default menuButton;
