import React, { useState, useEffect } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import useResizeAware from 'react-resize-aware';
import { AnimatePresence, motion } from 'framer-motion';

import Background from '../../components/Background/Background';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import NavBar from '../../components/NavBar/NavBar';
import MenuButton from '../../components/MenuButton/MenuButton';

import '../../scss/base.scss';
import classes from './Layout.module.scss';

const sideBarMenuVariants = {
  init1: {
    y: '6rem',
    opacity: 0,
  },
  open1: {
    y: 0,
    opacity: 1,
    transition: {
      type: 'spring',
      stiffness: 150,
      delay: 0.5
    },
  },
  init: {
    scaleX: 0,
    opacity: 0,
    originX: 0,
    originY: 0.5,
  },
  open: {
    scaleX: 1,
    opacity: 1,
    transition: {
      type: 'spring',
      delay: 0.7
    },
  },
  close: {
    y: '6rem',
    opacity: 0,
    transition: {
      type: 'spring',
    },
  },
};

const backdropVariants = {
  init: {
    opacity: 0,
  },
  open: {
    opacity: 1,
    transition: {
      type: 'tween',
      ease: 'easeInOut',
      duration: 1,
    },
  },
  close: {
    opacity: 0,
    transition: {
      type: 'tween',
      ease: 'easeInOut',
      duration: 0.5,
    },
  },
};

const Layout = ({ children, centerIt }) => {
  const [sideBarOpen, setSideBarOpen] = useState(false);
  const [resizeListener, sizes] = useResizeAware();

  useEffect(() => {
    if (sizes.width > 800) {
      setSideBarOpen(false);
    }
  }, [sizes.width]);

  let backdrop = null;
  let sideBarMenu = (
    <motion.div
      className={classes.NavBar}
      variants={sideBarMenuVariants}
      initial={(sizes.width > 800) ? "init1" : "init"}
      animate={(sizes.width > 800) ? "open1" : "open"}
      exit="close"
    >
      <NavBar />
    </motion.div>
  );
  let menuButton = null;

  const toggleSideBar = () => setSideBarOpen(prevState => !prevState);
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          developer
        }
      }
    }
  `);
  const mainBodyClassList = [classes.MainBody];

  if (centerIt) {
    mainBodyClassList.push(classes.CenterIt);
  }

  if (sizes.width <= 800) {
    menuButton = (
      <MenuButton
        noOfBranches="4"
        clicked={toggleSideBar}
        closed={!sideBarOpen}
      />
    );
    if (sideBarOpen) {
      backdrop = (
        <motion.div
          className={classes.Backdrop}
          role="button"
          tabIndex="0"
          onClick={setSideBarOpen.bind(this, false)}
          onKeyDown={setSideBarOpen.bind(this, false)}
          aria-label="close"
          variants={backdropVariants}
          initial="init"
          animate="open"
          exit="close"
        ></motion.div>
      );
    } else {
      sideBarMenu = null;
    }
  }

  return (
    <div className={classes.Layout} style={{ position: 'relative' }}>
      {resizeListener}
      <div className={classes.Header}>
        <Header headerName={data.site.siteMetadata.developer} />
      </div>
      <Background />
      <div className={classes.Main}>
        <main className={mainBodyClassList.join(' ')}>{children}</main>
        <AnimatePresence>{sideBarMenu}</AnimatePresence>
      </div>
      <div className={classes.Footer}>
        <Footer footerName={data.site.siteMetadata.developer} />
      </div>
      <AnimatePresence>{backdrop}</AnimatePresence>
      {menuButton}
    </div>
  );
};

export default Layout;
