import React from 'react';

import NavLink from '../NavLink/NavLink';

import classes from './NavBar.module.scss';

const reqSvgs = require.context('../../assets/icons/navbar', false, /\.svg$/);

const linksList = reqSvgs.keys().map(path => {
  const svgNameWordsArr = path.split('.')[2].split('-');
  const linkText = svgNameWordsArr.join(' ');
  const [to] = svgNameWordsArr;

  return (
    <li key={linkText}>
      <NavLink to={to} linkText={linkText}>
        {reqSvgs(path)(reqSvgs(path).defaultProps)}
      </NavLink>
    </li>
  );
});

const navBar = () => {
  return (
    <nav className={classes.NavBar}>
      <ul className={classes.NavBarList}>{linksList}</ul>
    </nav>
  );
};

export default navBar;
