import React from 'react';

import { H1, H1SubHeading } from '../Typography/Typography';
import Logo from '../Logo/Logo';

import classes from './Header.module.scss';

const header = ({ headerName }) => (
  <header className={classes.Header}>
    <div className={classes.HeaderLogo}>
      <Logo />
    </div>
    <div className={classes.HeaderHeading}>
      <H1>{headerName}</H1>
      <H1SubHeading>
        <span className={classes.HeaderSubHeading1}>Hi, there.</span>{' '}
        <span className={classes.HeaderSubHeading2}>
          Let's build together.
        </span>
      </H1SubHeading>
    </div>
    <div className={classes.HorizontalSeperator}></div>
  </header>
);

export default header;
