import React from 'react';
import Layout from './src/containers/Layout/Layout';

export const wrapPageElement = ({ element, props }) => {
  if (
    element.props.uri === '/about' ||
    element.props.uri === '/skills' ||
    element.props.uri === '/projects' ||
    element.props.uri === '/contact'
  ) {
    return (
      <Layout {...props} centerIt>
        {element}
      </Layout>
    );
  } else if (element.props.location.pathname === '/') {
    return <Layout {...props}>{element}</Layout>;
  }
  return element;
};
